import { useDispatch } from "react-redux";
import { AllergyTag } from "../../../../assets/constants/content_types/AllergyTag";
import { DietTag } from "../../../../assets/constants/content_types/DietTag";
import {
  addAllergyTag,
  addDietTag,
  clearAllPreferencesTags,
  removeAllergyTag,
  removeDietTag,
} from "../../../../redux/PreferencesReducer";
// import CustomCheckBox from '../checkbox/CustomCheckBox';
// import CloseButton, { CloseButtonSize } from '../close_button/CloseButton';
// import StandardButton, { StandardButtonColor, StandardButtonIconPosition, StandardButtonIconType } from '../standard_button/StandardButton';
import "../Checkboxes.css";
// import React, {useEffect, useMemo, useState} from "react";
import { useLocation } from "react-router-dom";
import { useSelector } from "react-redux";
import { RootState } from "../../../../redux/ReduxStore";
import { useMediaQuery } from "react-responsive";
import { MediaQueries } from "../../../../assets/constants/enums/MediaQueries";
import useHoverAndClick from "../../../../utils/hooks/UseHoverAndClick";
import { useEffect, useState } from "react";
import CustomCheckBox from "../../buttons/checkbox/CustomCheckBox";
import StandardButton, {
  StandardButtonColor,
  StandardButtonIconPosition,
  StandardButtonIconType,
} from "../../buttons/standard_button/StandardButton";

const DietCheckboxes = () => {
  const { isHovered, isClicked, ...eventHandlers } = useHoverAndClick();
  const [isOpened, setOpened] = useState(false);

  const isTouchable = useMediaQuery({ query: MediaQueries.TOUCHABLE });
  const isMobile = useMediaQuery({ query: MediaQueries.NORMAL_MOBILE });

  const preferences = useSelector((state: RootState) => state.preferences);
  const dispatch = useDispatch();
  const location = useLocation();

  useEffect(() => {
    if (!isOpened) {
      document.body.classList.remove("hidden");
      document.body.classList.remove("pref-btn-all-wrapper");
    } else {
      document.body.classList.add("hidden");
      document.body.classList.add("pref-btn-all-wrapper");
    }

    return () => {
      document.body.classList.add("pref-btn-all-wrapper");
      document.body.classList.remove("hidden");
    };
  }, [isOpened]);

  useEffect(() => {
    const handleResize = () => {
      setOpened(false);
    };

    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  useEffect(() => {
    setOpened(false);
  }, [location?.pathname]);

  const renderDietCheckboxes = () => {
    return Object.keys(DietTag).map((key) => {
      const val = DietTag[key as keyof typeof DietTag];
      return (
        <CustomCheckBox
          key={`${val}-checkbox`}
          onChange={(isChecked) => {
            if (isChecked) {
              dispatch(addDietTag(val));
            } else {
              dispatch(removeDietTag(val));
            }
          }}
          checked={preferences?.diet?.includes(val)}
          text={val}
        />
      );
    });
  };

  const renderMobile = () => {
    return (
      <div className={"diet-sheet-wrapper"} id={"diet-sheet-wrapper"}>
        <div
          className={"diet-tags-wrapper"}
          style={{ maxHeight: window.innerHeight - 280 }}
        >
          {renderDietCheckboxes()}
        </div>
      </div>
    );
  };

  const renderDesktop = () => {
    return (
      <>
        {/* <div
          className={"pref-popup"}
          style={{
            width: isOpened
              ? Math.min(Math.max(600, window.innerWidth - 350), 768)
              : 0,
          }}
        > */}
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            width: "100%",
            gap: "8px",
          }}
        >
          {renderDietCheckboxes()}
        </div>
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            width: "100%",
            justifyContent: "flex-end",
            gap: "8px",
          }}
        >
          <div
            className={"animation-01s-all"}
            style={{
              overflow: "hidden",
              maxWidth:
                preferences?.diet?.length > 0 ||
                preferences?.allergy?.length > 0
                  ? "300px"
                  : 0,
            }}
          >
            {/* {renderClearAllButton()} */}
          </div>
          <div></div>
        </div>
        {/* </div> */}
      </>
    );
  };

  return isMobile ? renderMobile() : renderDesktop();
};

export default DietCheckboxes;
