import "./PositionCard.css";
import { useMediaQuery } from "react-responsive";
import TagButton, { TagButtonType } from "../buttons/tag_button/TagButton";
import { MediaQueries } from "../../../assets/constants/enums/MediaQueries";
import {
  FoodPositionInfo,
  FoodPositionInOrderInfo,
} from "../../../assets/constants/content_types/FoodInfo";
import { useDispatch } from "react-redux";
import { setDisplayedFood } from "../../../redux/displayed_food_reducer/DisplayedFoodReducer";
import {
  addOrderPosition,
  decrementOrderPositionCount,
  incrementOrderPositionCount,
  OrderPosition,
  removeOrderPosition,
} from "../../../redux/current_order_reducer/CurrentOrderReducer";
import Toggle from "../toggle/Toggle ";

interface PositionCardProps {
  // orderPosition: OrderPosition | FoodPositionInfo;
  position: FoodPositionInOrderInfo | FoodPositionInfo;
  count?: number;
  // showVariationCount?: boolean;
  isMenu?: boolean;
}

const PositionCard = (props: PositionCardProps) => {
  const dispatch = useDispatch();
  const isMobile = useMediaQuery({ query: MediaQueries.NORMAL_MOBILE });
  const inOrder = props.count && props.count > 0;
  function foodVariationsLength(count: number) {
    if (!props.isMenu) {
      return null;
    }
    if (count > 1) {
      return count + " вариантa";
    } else {
      return count + " вариант";
    }
  }

  return (
    <div className="edit-order__item">
      <img
        className="order-img"
        src={props.position.image}
        alt={props.position.name}
        onClick={() => {
          dispatch(setDisplayedFood(props.position));
        }}
      />
      <div className="edit-order__item-cont">
        <div
          className="edit-order__item-text"
          onClick={() => {
            dispatch(setDisplayedFood(props.position));
          }}
        >
          <div className="position-title">
            {props.position.name}
          </div>
          <div className="edit-order__item-info subtext">
            <div className="no-wrap">Основное меню</div>
            <div>
              <div>
                {foodVariationsLength(props.position.foodVariations.length)}
              </div>
              <div>
                {props.position.foodVariations[0].price} ₽{" "}
                {props.count && props.count > 1
                  ? `* ${props.count} шт = ${
                      props.position.foodVariations[0].price * props.count
                    } ₽`
                  : ""}
              </div>
            </div>
          </div>
        </div>
        <div className="edit-order__item-btns">
          {inOrder ? (
            <TagButton
              onClickAction={() => {
                dispatch(
                  removeOrderPosition(props.position as FoodPositionInOrderInfo)
                );
              }}
              isActive={false}
              text={"Убрать"}
              withCross={true}
              type={isMobile ? TagButtonType.MOBILE : TagButtonType.DESKTOP}
              fullWidth={isMobile}
            />
          ) : null}
          {!props.isMenu ? (
            <TagButton
              onClickAction={() => {
                if (inOrder) {
                  dispatch(
                    incrementOrderPositionCount(
                      props.position as FoodPositionInOrderInfo
                    )
                  );
                } else {
                  dispatch(
                    addOrderPosition(props.position as FoodPositionInfo)
                  );
                }
              }}
              isActive={false}
              text={"+1"}
              type={isMobile ? TagButtonType.MOBILE : TagButtonType.DESKTOP}
            />
          ) : (
            <Toggle />
          )}
          {inOrder ? (
            <TagButton
              onClickAction={() => {
                dispatch(
                  decrementOrderPositionCount(
                    props.position as FoodPositionInOrderInfo
                  )
                );
              }}
              isActive={false}
              text={"-1"}
              type={isMobile ? TagButtonType.MOBILE : TagButtonType.DESKTOP}
            />
          ) : null}
        </div>
      </div>
    </div>
  );
};

export default PositionCard;
