import './Status.css'
// import '../../../../assets/styles/fonts.css'

interface StatusProps {
    // text: string;
    status?: 'expected' | 'paid' | 'canceled';
  }

const Status = (props: StatusProps) => {
    let Elem: JSX.Element = (
        <div></div>
    );

    if (props.status === 'expected') {
        Elem = (
            <div className="expected tags-text">
                Оплата ожидается
            </div>
        )
    } else if (props.status === 'paid') {
        Elem = (
            <div className="paid tags-text">
                Счёт оплачен
            </div>
        )
    } else if (props.status === 'canceled') {
        Elem = (
            <div className="canceled tags-text">
                Отменён
            </div>
        )
    }

    return (
        <> 
            {Elem}
        </>
    )
}

export default Status