import { useDispatch } from 'react-redux';
import { AllergyTag } from '../../../../assets/constants/content_types/AllergyTag';
import { DietTag } from '../../../../assets/constants/content_types/DietTag';
import { addAllergyTag, addDietTag, clearAllPreferencesTags, removeAllergyTag, removeDietTag } from '../../../../redux/PreferencesReducer';
// import CustomCheckBox from '../checkbox/CustomCheckBox';
// import CloseButton, { CloseButtonSize } from '../close_button/CloseButton';
// import StandardButton, { StandardButtonColor, StandardButtonIconPosition, StandardButtonIconType } from '../standard_button/StandardButton';
import '../Checkboxes.css'
// import React, {useEffect, useMemo, useState} from "react";
import { useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { RootState } from '../../../../redux/ReduxStore';
import { useMediaQuery } from 'react-responsive';
import { MediaQueries } from '../../../../assets/constants/enums/MediaQueries';
import useHoverAndClick from '../../../../utils/hooks/UseHoverAndClick';
import { useEffect, useState } from 'react';
import CustomCheckBox from '../../buttons/checkbox/CustomCheckBox';
import StandardButton, { StandardButtonColor, StandardButtonIconPosition, StandardButtonIconType } from '../../buttons/standard_button/StandardButton';


const AllergyCheckboxes = () => {
  const {isHovered, isClicked, ...eventHandlers} = useHoverAndClick()
  const [isOpened, setOpened] = useState(false)

  const isTouchable = useMediaQuery({ query: MediaQueries.TOUCHABLE });
  const isMobile = useMediaQuery({ query: MediaQueries.NORMAL_MOBILE })

  const preferences = useSelector((state: RootState) => state.preferences)
  const dispatch = useDispatch()
  const location = useLocation()

  useEffect(() => {
    if (!isOpened) {
      document.body.classList.remove('hidden')
      document.body.classList.remove('pref-btn-all-wrapper');
    } else {
      document.body.classList.add('hidden')
      document.body.classList.add('pref-btn-all-wrapper')
    }

    return() => {
      document.body.classList.add('pref-btn-all-wrapper')
      document.body.classList.remove('hidden')
    }
  }, [isOpened]);

  useEffect(() => {
    const handleResize = () => {
      setOpened(false)
    }

    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    setOpened(false)
  }, [location?.pathname]);





  const renderAllergyCheckboxes = () => {
    return Object.keys(AllergyTag).map(key => {
      const val =  AllergyTag[key as keyof typeof AllergyTag];
      return(
        <CustomCheckBox
          key={`${val}-checkbox`}
          onChange={isChecked => {
            if (isChecked) {
              dispatch(addAllergyTag(val))
            } else {
              dispatch(removeAllergyTag(val))
            }
          }}
          checked={preferences?.allergy?.includes(val)}
          text={val}
        />
      )
    })
  }

  // const renderClearAllButton = () => {
  //   return(
  //     <StandardButton
  //       onClickAction={() => {
  //         dispatch(clearAllPreferencesTags())
  //         setOpened(false)
  //       }}
  //       text={'Сбросить фильтрацию'}
  //       color={StandardButtonColor.GRAY}
  //       iconType={StandardButtonIconType.DARK_CROSS}
  //       iconPosition={StandardButtonIconPosition.BEFORE_TEXT}
  //     />
  //   )
  // }



  const renderMobile = () => {
    return(
      <div className={'pref-btn-all-wrapper'}>
              <div className={'diet-sheet-wrapper'} id={'diet-sheet-wrapper'}>
                {/* <div className={'top-grad'}/> */}
                {/* <div className={'mobile-h2 diet-header'}>Диетические предпочтения</div> */}
                <div className={'diet-tags-wrapper'} style={{ maxHeight: window.innerHeight - 280 }}>
                  {/* {renderDietCheckboxes()} */}
                  {/* <div className={'mobile-subtext gray-secondary'}>У меня аллергия на:</div> */}
                  {renderAllergyCheckboxes()}
                </div>
                {/* <div className={'btns-wrapper'}>
                  <div className={'bottom-grad'}/>
                  <div
                    className={'animation-02s-all'}
                    style={{
                      width: '100%',
                      display: 'flex',
                      opacity: (preferences?.diet?.length > 0 || preferences?.allergy?.length > 0) ? 1 : 0 ,
                      overflow: "hidden",
                      maxHeight: (preferences?.diet?.length > 0 || preferences?.allergy?.length > 0) ? '60px' : 0,
                      paddingTop: (preferences?.diet?.length > 0 || preferences?.allergy?.length > 0) ? '4px' : 0
                    }}
                  >
                  </div>
                </div> */}
              </div>
            {/* </Sheet.Content>
          </Sheet.Container>
          <Sheet.Backdrop/>
        </Sheet> */}
      </div>
    )
  }

  const renderDesktop = () => {
    return(
      <>
        {/* {buttonBody} */}
        {/* <div
          className={`pref-btn-desktop-absolute-wrapper ${isOpened && 'open'}`}
          onClick={e => {
            if (e.target === e.currentTarget) {
              setOpened(false)
            }
          }}
        > */}
          <div
            className={'pref-popup'}
            style={{ width: isOpened ? Math.min(Math.max(600, window.innerWidth - 350), 768) : 0 }}
          >
            {/* <div className={'header-and-close-wrapper'}>
              <div className={'header'}>
                Диетические предпочтения
              </div>
              <div style={{ marginLeft: '12px'}}>
                <CloseButton
                  size={CloseButtonSize.BIG}
                  onClickAction={() => setOpened(false)}
                  defaultColor={'transparent'}
                />
              </div>
            </div> */}
            {/* <div style={{ display: 'flex', flexDirection: "column", width: '100%', gap: '8px'}}>
              {renderDietCheckboxes()}
            </div> */}
            <div style={{ display: 'flex', flexDirection: "column", width: '100%', gap: '8px'}}>
              {/* <div className={'desktop-subtext text-secondary'}>У меня аллергия на:</div> */}
              {renderAllergyCheckboxes()}
            </div>
            <div style={{ display: 'flex', flexDirection: "row", width: '100%', justifyContent: 'flex-end', gap: '8px'}}>
              <div className={'animation-01s-all'} style={{ overflow: "hidden", maxWidth: (preferences?.diet?.length > 0 || preferences?.allergy?.length > 0) ? '300px' : 0}}>
                {/* {renderClearAllButton()} */}
              </div>
              <div>
                {/* {renderShowVariantsButtonWrapper()} */}
              </div>
            </div>
          </div>
        {/* </div> */}
      </>
    )
  }

  return isMobile ? renderMobile() : renderDesktop()
}

export default AllergyCheckboxes